.feed {
  background: #100e17;
}
.feed-wrapper {
  width: 60%;
  margin: 0 auto;
}
.post-card {
  padding: 4rem 0;
  border-bottom: 2px solid #201c29;
  background: #100e17;
}
.post-type-date {
  display: flex;
}

.post-title {
  color: white;
  font-size: 2rem;
  font-weight: 700;
  line-height: 1.5;
  letter-spacing: 0.08rem;
}
.post-profile {
  display: flex;
  align-items: center;
  margin: 1.6rem 0;
}
.post-profile-pic {
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  padding: 0.3rem;
  border: 2px solid red;
}
.post-author-div {
  margin: 0 2rem;
}
.post-author {
  color: #7a7a8c;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.08rem;
  line-height: 1.5;
}
.post-author-name {
  color: white;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.08rem;
  line-height: 1.5;
}
.post-type {
  color: #7a7a8c;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.08rem;
  line-height: 1.5;
}
.post-time {
  color: white;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.08rem;
  line-height: 1.5;
}
.post-descri {
  line-height: 1.5;
  color: #c2c2c2;
  overflow-wrap: break-word;
  font-size: 1.2rem;
  font-weight: 400;
}
.post-readmore {
  line-height: 1.5;
  background: linear-gradient(90deg, #ff8a00, #e52e71);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 700;
  font-size: 1.3rem;
  letter-spacing: 0.08rem;
  border-bottom: 1px solid #e52e71;
  padding-bottom: 0.15rem;
  cursor: pointer;
}
.post-delete {
  background: linear-gradient(90deg, #ff8a00, #e52e71);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-left: 1rem;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 0.08rem;
  cursor: pointer;
}
