.article {
  background: #100e17;
  border-bottom: 5px solid red;
  border-bottom-right-radius: 8rem;
}
.article-wrapper {
  width: 90%;
  padding: 2rem 0;
  margin: 0 auto;
}
.article-card {
  padding: 4rem 0;
}
.article-type-date {
  display: flex;
}
.article-type {
  color: #7a7a8c;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.08rem;
  line-height: 1.5;
}
.article-time {
  color: white;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.08rem;
  line-height: 1.5;
}

.article-title {
  color: white;
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 1.5;
  letter-spacing: 0.08rem;
}
.article-profile {
  display: flex;
  align-items: center;
  margin: 1.6rem 0;
}
.article-profile-pic {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  padding: 0.5rem;
  border: 2px solid #7a7a8c;
}
.article-author-div {
  margin: 0 2rem;
}
.article-author {
  color: #7a7a8c;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.08rem;
  line-height: 1.5;
}
.article-author-name {
  color: white;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.08rem;
  line-height: 1.5;
}
.article-descri {
  line-height: 1.5;
  color: #c2c2c2;
  overflow-wrap: break-word;
  font-size: 1.2rem;
  font-weight: 400;
}
.article-body-wrapper {
  width: 90%;
  padding: 3rem 0 2rem 0;
  margin: 0 auto;
}
.article-body {
  color: #2e2f3e;
  line-height: 1.5;
  letter-spacing: 0.08rem;
  font-size: 1.2rem;
  font-weight: 400;
}
