.signup {
  background: #100e17;
  height: 100vh;
}
.signup-wrapper {
  width: 30%;
  padding: 2rem 0;
  margin: 0 auto;
}
.signup-text {
  color: white;
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: 0.08rem;
  margin-bottom: 2rem;
}
.signup-input {
  display: block;
  margin: 1.5rem 0;
  width: 100%;
  padding: 1rem 0 1rem 0.5rem;
  border-radius: 0.5rem;
  border: none;
  font-size: 1rem;
  font-weight: 400;
}
.signup-btn {
  display: block;
  margin: 1.5rem 0;
  width: 100%;
  padding: 1rem 0;
  text-align: center;
  border-radius: 0.5rem;
  border: none;
  font-size: 1.2rem;
  font-weight: 700;
  background-color: tomato;
  color: white;
  letter-spacing: 0.08rem;
}
