.profile {
  background: #100e17;
  width: 100%;
  margin: 0 auto;
}
.profile-wrapper {
  width: 90%;
  padding: 2rem 0;
  margin: 0 auto;
}
.profile-details {
  display: flex;
  align-items: center;
  width: 70%;
  border-bottom: 5px solid red;
  border-bottom-right-radius: 3rem;
  border-right: 5px solid red;
  padding: 3rem 0;
}
.profile-pic {
  width: 148px;
  height: 148px;
  border-radius: 50%;
  padding: 1rem;
  border: 1px solid red;
}
.profile-name {
  color: white;
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 1.5;
  margin-left: 2rem;
}
