.header {
  background: #100e17;
  width: 100%;
  margin: 0 auto;
  border-bottom: 2px solid #201c29;
}
.header-wrapper {
  width: 90%;
  padding: 2rem 0 1rem 0;
  margin: 0 auto;
}
.nav-bar {
  background: #100e17;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-logo {
  font-size: 2rem;
  letter-spacing: 0.08rem;
  font-weight: 700;
  background: linear-gradient(90deg, #ff8a00, #e52e71);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.nav-item {
  margin-left: 1.5rem;
  color: #c2c2c2;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 0.08rem;
}
.nav-items-right {
  display: flex;
  align-items: center;
}
.twitter {
  margin-left: 0.5rem;
}
.footer {
  display: flex;
  align-items: center;
}
.footer-text {
  color: hsl(0, 0%, 76%);
  font-size: 0.7rem;
  font-weight: 300;
  letter-spacing: 0.08rem;
}
.footer-text-name {
  background: linear-gradient(90deg, #ff8a00, #e52e71);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 0.8rem;
  font-weight: 500;
  letter-spacing: 0.08rem;
  margin-left: 0.3rem;
}
.footer-link {
  color: #c2c2c2;
  font-size: 0.7rem;
  font-weight: 300;
  letter-spacing: 0.08rem;
  margin-left: 2rem;
}
